import React from "react"
import {injectIntl} from "gatsby-plugin-intl"
import Layout from "../components/layout"
import Seo from "../components/seo"
import * as styles from "../styles/index.module.css";

const Privacy = ({intl}) => (
  <Layout>
    <div className={styles.mainWrapper}>
      <Seo lang={intl.locale} title="Policy - The Best Youtube Downloader"/>
      <div className={styles.contact}>
        <p><strong>PRIVACY POLICY</strong></p>
        <p>Here at yt1s Office, doing business as yt1s (“Company,” “we,” “us,” “our”), we know that our users care about
          how your personal information is used and shared. We take your privacy seriously. Please read the following to
          learn more about our Privacy Policy.</p>
        <p>By visiting or using the Services in any manner, you acknowledge that you accept the practices and policies
          outlined in this Privacy Policy, and you hereby consent that we will collect, use, process and share your
          information as described herein.</p>
        <p>Any capitalized terms used herein without definition shall have the meaning given to them in the End User
          License Agreement: <a href="">yt1shelp@gmail.com</a> .</p>
        <p>I. PRIVACY PRINCIPLES</p>
        <p>This Privacy Policy reflects the following three principles that we strive to uphold in providing you with
          the services on our Services:</p>
        <ul>
          <li>We will only collect information that helps us to improve our Services;</li>
          <li>You may provide and we only collect information that can identify you personally(“Personal Information”)
            in order to provide the Services;
          </li>
          <li>Our privacy practices will be transparent to you.</li>
        </ul>
        <p>II. WHAT DOES THIS PRIVACY POLICY COVER?</p>
        <p>This Privacy Policy covers our treatment of information that we gather when you are accessing or using our
          Services. This policy does not apply to the practices of companies that we do not own or control (including,
          without limitation, the third party content providers from whom you may receive content through the Services),
          or to individuals that we do not employ or manage.</p>
        <p>Company does not knowingly collect or solicit Personal Information from anyone under the age of 13 or
          knowingly allow such persons to register. If you are under 13, please do not attempt to register for the
          Website or send any information about yourself to us, including your name, address, telephone number, or email
          address. No one under age 13 may provide any personal information to Company or on the Website. In the event
          that we learn that we have collected personal information from a child under age 13 without verification of
          parental consent, we will delete that information as quickly as possible. If you believe that we might have
          any information from or about a child under 13, please contact us at <a
            href="https://yt1s.works">yt1shelp@gmail.com</a>.
        </p>
        <p>III. WHAT INFORMATION DOES yt1s COLLECT?</p>
        <p>The information we collect can be generally placed in two categories: 1) data on how the user uses our
          Services; 2) aggregate data on content the user engages with. We may use this information to personalize and
          improve our services, to fulfill your requests for certain products and services, and to analyze how you use
          the Services. We may provide this information to our partners, so that they, too, can provide you with an
          optimal experience, but we never disclose information to a partner in a manner that would identify you
          personally. Here are a few examples of data we collect when you use our Services:</p>
        <ul>
          <li>Whenever you interact with our website, we automatically receive and record information on our server logs
            from your browser including your IP address, “cookie” information, and the page you requested. “Cookies” are
            identifiers we transfer to your computer or mobile device that allow us to recognize your computer or device
            and tell us how and when the Services are visited and by how many people. You may be able to change the
            preferences on your browser or device to prevent or limit your computer or device’s acceptance of cookies,
            but this may prevent you from taking advantage of some of our features. We may use Google Analytics to
            gather non-Personal Information. In such cases, you may have the option to disable certain features as
            described below.
          </li>
          <li>Google Analytics is a web analytics service provided by Google, Inc. ("Google"). Google Analytics uses
            cookies to help the website analyze how individuals use the Website and Apps. The non-Personal Information
            generated by the cookie about your use of the site will be transmitted to and stored by Google on servers in
            the United States of America (“USA”). Google will use this information for the purpose of evaluating your
            use of the site, compiling reports on site activity for our purpose, evaluation and providing other services
            relating to the site activity and Internet usage. Google will not associate your IP address with any other
            data held by Google. You may disable cookies within your browser, however please note that if you do this
            you may not be able to use the full functionality of the site. By using this site, you consent to the
            processing of data about you by Google in the manner and for the purposes set out above. For certain
            browsers, you can also prevent Google from collecting information (including your IP address) via cookies
            and processing this information by downloading this browser plugin and installing it:
            http://tools.google.com/dlpage/gaoptout.
          </li>
          <li>Whenever you interact with yt1s, we automatically receive and record information on our server logs, for
            example:
            <ol>
              <li>Services usage data. For example, this may include information regarding the version of our software
                you are using, the location from where you downloaded our software, how often you launch our software,
                which features you use, and the number of videos you have downloaded. We use this to improve our
                promotion and help more users discover Company.
              </li>
              <li>Data on the quality of our Services. For example, this may include information regarding crashes and
                your connection between your phone and your computer. This data allows us to make our software more
                stable and faster.
              </li>
              <li>Data on user downloads. For example, this may include information regarding which videos you download
                and your average download speed. This allows us to choose our content partners, and alerts us if
                downloads become slow or unreliable.
              </li>
              <li>Aggregate statistics on content. For example, this may include the number of contacts you have on your
                phone, how many contacts you have marked as favorites, and the number of apps, songs, videos, or other
                content you have on your phone. This information is only collected as aggregate data, without reference
                to your or your contacts’ identities.
              </li>
              <li>Hardware and software identification. For example, this may include the type of mobile phone you are
                using, the version of Android operating system, the size of your device’s screen and internal storage.
                We support over 500 different Android devices and this helps us reach decisions as to how to prioritize
                support for different kinds of hardware and software.
              </li>
            </ol>
          </li>
          <li>A few particular features to note:
            <ol>
              <li>If you're using our software and it crashes, the crash report sent to our server may contain some
                information about the operating system that you use.
              </li>
              <li>If you download content via our desktop software, we store a copy to your computer so that the next
                time you connect your phone you don't have to download it again.
              </li>
              <li>If you provide us with your name and email address, we will use this information for the purpose of
                contacting you regarding our services and products. You may opt out of such communications at any time
                by unsubscribing through our website or contacting us through one of the methods listed at the end of
                this policy.
              </li>
            </ol>
          </li>
        </ul>
        <p>The data we collect is sent to the Company servers in the USA in aggregate form and added to our system for
          analysis. Our Services do NOT collect the following information:</p>
        <ul>
          <li>Your phone number</li>
          <li>Your friends' phone numbers</li>
          <li>Your cat's phone number</li>
          <li>Any of the content of your text messages, either sent or received</li>
          <li>Your music, photos, or videos</li>
          <li>The content you download via Company</li>
        </ul>
        <p>We do not purport to have listed all possible disclosures that we might make. This Privacy Policy is intended
          to help you understand our general practices. This policy is not a promise that your information will never be
          disclosed except as described above. For example, third parties may unlawfully intercept or access information
          transmitted to or contained on the site, technologies may malfunction or not work as anticipated, or someone
          might access, abuse or misuse information, despite a lack of permission. Although we use what we believe to be
          commercially reasonable practices to protect your privacy, that does not mean, and you should not expect it to
          mean, that your information or communications will always be private or protected.</p>
        <p>We generally will retain information for as long as required, allowed or for as long as we believe it useful.
          If you cease using the website, or your permission to use the website is terminated, we may continue to use
          and disclose your Personal Information in accordance with this Privacy Policy as amended from time to time. We
          do not undertake retention obligations through this Privacy Policy. We may dispose of information in our
          discretion without notice, subject to applicable law.</p>
        <p>IV. DO WE DISCLOSE THE INFORMATION WE COLLECT TO OUTSIDE PARTIES?</p>
        <p>In addition to the disclosures described herein, we may release your information when we believe release is
          appropriate to comply with the law, enforce our site policies, or protect ours or others' rights, property, or
          safety. Subject to applicable law, we reserve the right voluntarily or involuntarily to make all lawful,
          worldwide uses of Personal Information, including without limitation, to: collect, use, access (or bar
          access), process, fulfill, disclose, display, share, respond to legal process or otherwise exercise our rights
          under applicable law, transfer, store, sell, lease, retain, commingle, investigate, verify, prove, enforce,
          delete, and otherwise deal with Personal Information, and information other than Personal Information.</p>
        <p>V. DISCLAIMER REGARDING SECURITY AND THIRD PARTY SITES</p>
        <p>WE DO NOT GUARANTEE THE SECURITY OF PERSONAL INFORMATION OR OTHER INFORMATION IN ANY FORM. We do implement a
          variety of security measures to maintain the safety of your Personal Information. Your Personal Information is
          contained behind secured networks and is only accessible by a limited number of persons who have special
          access rights to such systems, and are required to keep the information confidential. When you place orders or
          access your Personal Information, we offer the use of a secure server. All sensitive information you supply is
          transmitted via Secure Socket Layer (SSL) technology and then encrypted into our databases to be only accessed
          as stated above.</p>
        <p>In an attempt to provide you with increased value, we may include third party links on our site. These linked
          sites have separate and independent privacy policies. We therefore have no responsibility or liability for the
          content and activities of these linked sites. Nonetheless, we seek to protect the integrity of our site and
          welcome any feedback about these linked sites (including if a specific link does not work).</p>
        <p>VI. ARE YOU VISITING OUR SITE FROM OUTSIDE THE UNITED STATES?</p>
        <p>If you are visiting our web site or purchasing products from outside of the USA otherwise contacting us from
          outside of the USA please be aware that your Personal Information may be transferred to, stored or processed
          in the USA, where our servers are located and our central database is operated. The data protection and other
          laws of the USA and other countries might not be as comprehensive as those in your country. By using our site
          or purchasing our products, you understand that your information may be transferred to our facilities and
          those third parties with whom we share it as described in this Privacy Policy.</p>
        <p>VII. CHANGES TO THIS PRIVACY POLICY</p>
        <p>We may amend this Privacy Policy from time to time. Use of information we collect now is subject to the
          Privacy Policy in effect at the time such information is used. If we make changes in the way we use Personal
          Information, we will notify you by posting an announcement on our Website or sending you an email. You are
          bound by any changes to the Privacy Policy when you use the Services after such changes have been first
          posted.</p>
        <p>VIII. HOW CAN YOU OPT-OUT, REMOVE OR MODIFY INFORMATION YOU HAVE PROVIDED TO US?</p>
        <p>To modify your email subscriptions, please let us know by modifying your preferences in your account or by
          sending us an email at the address below. Please note that due to email production schedules you may receive
          any emails already in production. To delete all of your online account information from our database please
          contact us directly by email at info@yt1s.works. Subject to applicable laws, please note that we may maintain
          information about an individual sales transaction in order to service that transaction and for record
          keeping.</p>
        <p>IX. CONSENT TO ELECTRONIC NOTICE IF THERE IS A SECURITY BREACH</p>
        <p>If we are required to provide notice of unauthorized access or other invasion of certain security systems,
          you agree that we (or they) may do so when required (or voluntarily) by posting notice on our web site or
          sending notice to any email address we have for you, in our (or their) good faith discretion. You agree that
          notice to you will count as notice to others for whom you are acting, and agree to pass the notice on to
          them.</p>
        <p>X. QUESTIONS OR CONCERNS</p>
        <p>If you have any questions or concerns regarding our privacy policies, please send us a detailed message to
          : <a href="https://yt1s.works">yt1shelp@gmail.com</a></p>
      </div>
    </div>
  </Layout>
);

export default injectIntl(Privacy)